/* Ensure the body and html take up full height */
html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: #adbdd7;
  font-family: Arial, sans-serif;
}

/* App container should also take full height */
.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh; /* Ensure app covers full viewport */
}

/* Header styles */
header {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: #669bbc;
  padding: 1em;
}

header nav ul {
  list-style: none;
  display: flex;
  justify-content: space-around;
}

header nav ul li {
  display: inline;
  font-size: large;
}

header nav ul li a {
  color: #ffffff;
  text-decoration: none;
  font-size: large;
}

/* Main content takes up the remaining height */
main {
  flex: 1;
  padding: 2em;
}

/* Section margins */
section {
  margin-bottom: 2em;
}

/* Footer styling to ensure it sticks at the bottom */
footer {
  background-color: #669bbc;
  color: white;
  text-align: center;
  padding: 1em 0;
  width: 100%;
  margin-bottom: 0;
}
